/**
 * 
 */
#wpbody-content {
  background-color: #F9F9F9;
  padding-bottom: 0; }

#wpcontent {
  padding-left: 0 !important; }

#wpfooter {
  display: none; }

#app-wrap {
  font-family: "Open Sans" !important;
  font-weight: 300 !important; }

body.toplevel_page_bluehost {
  background-color: #F9F9F9; }

.screen-reader-shortcut.bluehost-spa-skip:focus {
  top: 5px;
  left: 5px; }

body:not(.bluehost-admin-keynav) .app-page:focus {
  outline: none; }

#route-wrap {
  background-color: #f1f5f7;
  padding: 1rem; }
  @media (min-width: 900px) {
    #route-wrap {
      padding: 1.66rem; } }
  @media (min-width: 1200px) {
    #route-wrap {
      padding: 2rem; } }

#bluehost-app {
  display: block;
  height: 100%;
  min-height: 100%; }
  #bluehost-app .pure-g [class*="pure-u"] {
    font-family: 'Open Sans' !important;
    font-weight: 300; }

#app-loader {
  display: flex;
  align-items: center;
  justify-content: center; }
  #app-loader .spinner-wrap {
    max-width: 50%;
    position: fixed;
    top: 48%; }
    #app-loader .spinner-wrap .components-spinner {
      background: #3575D3;
      opacity: 1;
      width: 36px;
      height: 36px;
      margin: 10px 22px 0;
      box-shadow: 0px 0px 1.33rem 0.66rem #BEE0F6; }
      #app-loader .spinner-wrap .components-spinner:before {
        top: 6px;
        left: 6px;
        width: 8px;
        height: 8px;
        transform-origin: 12px 12px; }
